.project-card {
    display: flex;
    height: 225px;

    overflow: hidden;
    border-radius: 5px;
    position: relative;

    transition: transform 0.25s;
}
.project-card:hover{
    transform: scale(1.05);
}

.card-image {
    width: 100%;
    height: 100%;
    object-fit: cover;
}
.card-footer {
    bottom: 0;
    width: 100%;
    padding: 0.5rem;

    display: flex;
    align-items: center;
    justify-content: space-between;
    flex-direction: row;

    position: absolute;
    box-sizing: border-box;

    background-color: var(--background-faded );
}
.footer-main{
    display: flex;
    flex-direction: column;
}
.footer-link a{
    display: flex;
    flex-direction: row;
    align-items: center;
    line-height: 1rem;
    font-size: 1rem;
    height: 1rem;
    color: var(--main-color);
}
